@media (min-width: 320px) {
  .quiz {
    &__page {
      display: flex;
      flex-direction: column;
      align-items: stretch;
    }
    &__title {
      font-size: $fz-mob-xxm;
      text-transform: uppercase;
      text-align: center;
      font-family: $ff-secondary;
      font-weight: 400;
      margin-bottom: get-vw(4);
    }
    &__subtitle {
      font-size: $fz-mob-s;
      text-align: center;
    }
    &__title + &__subtitle {
      margin-top: get-vw(7);
    }
    &__subtitle + &__form {
      margin-top: get-vw(34);
    }
    &__options {
      flex-grow: 1;
      display: flex;
      margin-bottom: get-vw(20);
      &--images {
        flex-wrap: wrap;
        justify-content: space-between;
      }
      &--images > .quiz__item {
        flex-basis: calc(50% - #{get-vw(10)});
        flex-shrink: 0;
        flex-grow: 0;
      }
      &--text {
        flex-direction: column;
        align-items: stretch;
      }
    }
    &__item {
      // &.quiz-item--img
    }
    &__actions {

    }
    &__form {}
    &__input {}
    &__input {
      height: get-vw(40);
      & + & {
        margin-top: get-vw(16);
      }

      & > input {
        font-size: $fz-mob-s;
        border: 1px solid $color-grey2;
        padding: get-vw(8) get-vw(30) get-vw(8);
        transition: .25s;
        height: 100%;
        color: $color-dark;
        max-width: none;
        width: 100%;
      }
      & > input::placeholder {
        color: $color-grey2;
      }
      & > input:focus {
        outline: none;
        border: 1px solid $color-dark;
      }
    }
    &__submit {
      padding: get-vw(10);
      margin-top: get-vw(34);
      font-size: $fz-mob-xxm;
      text-transform: uppercase;
    }
    &__policy {
      color: $color-grey2;
      font-size: $fz-mob-xs;
      text-align: center;
      max-width: 85%;
      margin: 0 auto;
      margin-top: get-vw(17);
    }
  }

  .quiz-actions {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    &__prev {
      background-color: transparent;
      border: none;
      margin-right: get-vw(25);
      & > img {
        width: get-vw(27);
      }
    }
    &__next {
      background-color: $color-accent;
      border: 1px solid $color-accent;
      text-transform: uppercase;
      color: $color-light;
      font-size: $fz-mob-xm;
      padding: get-vw(6) get-vw(30);
      transition: .25s;
      // &:active {
      //   color: $color-light;
      //   background-color: $color-accent;
      // }
      &[disabled] {
        color: $color-accent;
        background-color: transparent;

      }
    }

  }



  .quiz-item {
    display: flex;
    transition: .25s;
    &--img {
      flex-direction: column;
      justify-content: space-between;
      &:nth-of-type(n+3) {
        margin-top: get-vw(15);
      }
      &.active, &:active {
        outline: 1px solid $color-accent;
      }
    }
    &--img .quiz-item__title {
      text-align: center;
    }
    &--text {
      justify-content: flex-start;
      align-items: center;
      border: 1px solid $color-grey2;
      padding: get-vw(8) get-vw(30) get-vw(8);
      transition: .25s;
      max-height: get-vw(40);
      height: 100%;
      &:not(:last-child) {
        margin-bottom: get-vw(16);
      }
      &.active, &:active {
        background-color: $color-accent;
        color: $color-light;
        border: 1px solid $color-accent;
      }
    }
    &--text .quiz-item__title {
      text-align: left;
    }
    &__img {
      margin-bottom: get-vw(4);
      width: 100%;
    }
    &__input {
      display: none;
    }
    &__title {
      font-size: $fz-mob-s;
      text-align: center;
    }

  }
}