.modal {
  display: none;
  &.active {
    display: block;
    // animation: showModal .25s forwards;
  }
  &.showing {
    animation: showModal .25s ease-out 1 forwards;
  }
  &.hiding {
    animation: hideModal .25s ease-out 1 forwards;
  }
}

@keyframes showModal {
  0% {
    opacity: 0;
    transform: scale(0.8) translateY(10%);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0%);
  }
}
@keyframes hideModal {
  0% {
    opacity: 1;
    transform: scale(1) translateY(0%);
  }
  100% {
    opacity: 0;
    transform: scale(0.8) translateY(10%);
  }
}


